<script setup>
import Copy from '@/components/Copy.vue';
import { uncode } from '../../helper';
import { reactive } from 'vue';
import store from '@/store';
import { useRouter } from 'vue-router';
import { computed } from '@vue/runtime-core';
const router = useRouter();
const props = defineProps({
    showModel: {
        type: Boolean,
        required: true
    },
    toggleModel: {
        type: Function,
        required: true
    }
});
const message = reactive({
    error: '',
    success: '',
    input: ''

})
const unlock = () => {
    message.error = '';
    message.success = '';
    console.log(store.getters.getUuid);
    console.log(store.getters.getUuid);
    // let unlock = store.getters.getUuid + '-' + message.input;
    let unlock = uncode(store.getters.getUuid, message.input);
    // let source = store.getters.getUuid + '-' + store.getters.getUnlockKeys[store.getters.getSector];
    let source = uncode( store.getters.getUuid, store.getters.getUnlockKeys[store.getters.getSector] );
    if (unlock == source) {
        message.success = 'Unlock အောင်မြင်သည်။ ကျေးဇူးအထူးတင်ရှိပါသည်။';
        localStorage.setItem('license', unlock);
        router.go(0);
    } else {
        message.error = 'unlock ကုတ်မှားယွင်းနေပါသည်။';
    }

};


</script>
<template>
    <!-- Main modal -->
    <div v-show="showModel"  class="overflow-y-auto overflow-x-hidden fixed top-0 right-0 left-0 z-50 w-full md:inset-0 h-full md:h-full">
        <div @click="toggleModel()" class="absolute bg-gray-500 opacity-60 w-full h-full">
        </div>
        <div  class="relative mx-auto p-4 w-full max-w-2xl h-full md:h-auto">
            <!-- Modal content -->
            <div class="relative bg-white rounded-lg shadow dark:bg-gray-700">
                <!-- Modal header -->
                <div class="flex justify-between items-start p-5 rounded-t border-b dark:border-gray-600">
                    <h3 class="select-none text-xl font-semibold text-gray-900 lg:text-2xl dark:text-white">
                        ဝယ်ယူသုံးစွဲမည်
                    </h3>
                    <button @click="toggleModel()" type="button"
                        class="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"
                        data-modal-toggle="defaultModal">
                        <svg class="w-5 h-5" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
                            <path fill-rule="evenodd"
                                d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                                clip-rule="evenodd"></path>
                        </svg>
                    </button>
                </div>
                <!-- Modal body -->
                <div class="p-6 space-y-6">
                    <p class="select-none text-sm text-justify leading-relaxed text-gray-500 dark:text-gray-400">
                        အောက်တွင်ဖော်ပြထားသော code ကို ကူးယူ၍ <span class="text-sm font-bold text-blue-500">Integrated
                            Solution FaceBook Page</span> ကို message ပေးပို့ပြီးဝယ်ယူနိုင်ပါတယ်။
                    </p>
                    <div
                        class="rounded-lg text-center text-blue-500 font-bold bg-pink-200 border-2 flex flex-row items-center justify-center">
                        {{store.getters.getReqKeys[store.getters.getSector]}}
                        <Copy :text="store.getters.getReqKeys[store.getters.getSector]" id_name="#rq_code"
                            class="text-blue-800 ml-3 my-2 border-blue-500" />
                    </div>
                    <p class="select-none text-sm text-justify leading-relaxed text-gray-500 dark:text-gray-400">
                        ဝယ်ယူထားသော unlock code ကိုအောက်ပါ Input Field တွင်ထည့်၍ Unlock ကိုနိပ်ပြီး
                        ဆက်လက်ဆောင်ရွက်နိုင်ပါသည်။
                    </p>
                    <input v-model="message.input" class="w-full rounded-full text-center border-gray-500 border-2 px-6 text-lg"
                        type="text">

                    <div class="text-sm text-red-500" v-if="message.error">{{message.error}}</div>
                    <div class="text-sm text-blue-500" v-if="message.success">{{message.success}}</div>
                </div>
                <!-- Modal footer -->
                <div class="flex justify-center p-6 space-x-2 rounded-b border-t border-gray-200 dark:border-gray-600">
                    <button @click="unlock" type="button"
                        class="select-none text-white bg-gradient-to-r from-indigo-500 to-blue-500 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">Unlock</button>
                </div>
            </div>
        </div>
    </div>
</template>

