import { deepClone } from '../../helper';
import j2010_07 from "./j2010_07";
import j2011_07 from './j2011_07';
import j2011_12 from './j2011_12';
import j2016_12 from './j2016_12';
import j2017_07 from './j2017_07';
const  state= {
    j2010_07,
    j2011_07,
    j2011_12,
    j2016_12,
    j2017_07
};
const getters= {
    j2010_07: (state) => state.j2010_07,
    j2011_07: (state) => state.j2011_07,
    j2011_12: (state) => state.j2011_12,
    j2016_12: (state) => state.j2016_12,
    j2017_07: (state) => state.j2017_07
};
const mutations= {};
const actions= {
    getQuestions: ({ getters, rootState, rootGetters })=>{
        if(rootGetters.getCurrentYear){
            return deepClone( getters[rootGetters.getCurrentYear] );
        }else{
            return null;
        }
    },
    getMoji: ({ getters, rootState, rootGetters })=>{
        if(rootGetters.getCurrentYear){
            return deepClone( getters[rootGetters.getCurrentYear].moji );
        }else{
            return null;
        }
    },
    getBunpou: ({ getters, rootState, rootGetters })=>{
        if(rootGetters.getCurrentYear){
            return deepClone( getters[rootGetters.getCurrentYear].bunpou );
        }else{
            return null;
        }
    },
    getChoukai: ({ getters, rootState, rootGetters })=>{
        if(rootGetters.getCurrentYear){
            return deepClone( getters[rootGetters.getCurrentYear].choukai );
        }else{
            return null;
        }
    },
    getScores: ({ getters, rootState, rootGetters }) => {
        if (rootGetters.getScores) {
            return deepClone( getters[rootGetters.getScores])
        }
    }
};
export default {
    state, getters, mutations, actions
};