const getters= {
    getCurrentYear: state=> state.currentYear,
    getCurrentTitle: state=> state.currentTitle,
    getTimeLeft: state=> state.timeLeft,
    getScores: state => state.scores,
    getUuid: state => state.uuid,
    getSector: state => state.sector,
    getHaveLicense: state => state.haveLicense,
    getReqKeys: state => state.req_keys,
    getUnlockKeys: state => state.unlock_keys
};
const mutations = {
    setCurrentYear: (state, year) => state.currentYear = year,
    setCurrentTitle: (state, title) => state.currentTitle = title,
    setTimeLeft: (state, time) => state.timeLeft = time,
    setScores: (state, scores) => state.scores = scores,
    setUuid: (state, id) => state.uuid = id,
    setSector: (state, sector) => state.sector = sector,
    setHaveLicense: (state, license) => state.haveLicense = license
};
const actions= {
     serviceInit: ({ commit,getters }) => {
        commit('setCurrentYear', localStorage.getItem('currentYear') || '');
        commit('setCurrentTitle', localStorage.getItem('currentTitle') || '' );
        commit('setTimeLeft', JSON.parse(localStorage.getItem('timeLeft')) || -1);
        commit('setSector',JSON.parse(localStorage.getItem('sector')) || -1);
        let cacheScore = JSON.parse(localStorage.getItem('scores'));
        // {"j2010_07":{"vocab_knowledge":2,"grammer_reading":1,"listening":16}}
        if (cacheScore) {
             //console.log("True", cacheScore);    
            let scores = deepClone(getters.getScores);
            if (scores.length != Object.keys(cacheScore).length) {
                //console.log("True", cacheScore);
                for (const key in (scores)) {
                    if (cacheScore.hasOwnProperty(key)) {
                        scores[key] = cacheScore[key];
                    }
                }
            }
            // Check length  localStorage == Dev Store?
            // != Take Consistency & Update it
            // Key & Value looping(For in)            
            commit('setScores', scores);
        }
    },
    setCurrentYear: ({commit},year)=>{
        localStorage.setItem('currentYear',year);
        commit('setCurrentYear',year);
    },
    setCurrentTitle: ({commit},title)=>{
        localStorage.setItem('currentTitle',title);
        commit('setCurrentTitle',title);
    },
    setTimeLeft: ({commit},time)=>{
        localStorage.setItem('timeLeft', JSON.stringify( time ) );
        commit('setTimeLeft',time);
    },
    setScores: ({ commit}, scores) => {
        localStorage.setItem('scores', JSON.stringify( scores ) );
        commit('setScores',scores);
    },
     unlock({ commit},key){
      localStorage.setItem('license', key);
      commit('setHaveLicense',true);
    },
    auth({ commit ,getters}){
      const uuid = new DeviceUUID().get();
      console.log(uuid);
      commit('setUuid',uuid);      
      // random sactor
      if (localStorage.getItem('sector')) {
        console.log("sector is present");
        const sector = localStorage.getItem('sector');
        commit('setSector', sector);
        console.log(sector);
      } else {
        console.log("sector is missing");
        const random = Math.floor(Math.random() * 200);
        localStorage.setItem('sector', random);
        commit('setSector', random);
      }

      if(localStorage.getItem('license')){
        // check license code
        const license = localStorage.getItem('license');
        const sector = localStorage.getItem('sector');
        // const key = uuid + '-' + getters.getUnlockKeys[sector];
        const key = uncode( uuid , getters.getUnlockKeys[sector]);

        console.log(key);
        console.log(license);
        if(license==key){
          commit('setHaveLicense', true);
          console.log("Have License");
        }else{
          commit('setHaveLicense', false);  
          console.log("No License");
        }
      }else{
        // not licenses
        commit('setHaveLicense',false);
      }
    }
};
export default {
    state, getters, mutations, actions
};