<script setup>
import { mdiLock } from '@mdi/js';
import Icon from '@/components/Svg.vue';
import LicenseModel from '@/components/Button/LicenseModel.vue';
import actionTypes from '@/store/actionTypes.js';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import { computed } from '@vue/runtime-core';
const router = useRouter();
const store = useStore();
const props = defineProps({
    isFree: {
        type: Boolean,
        required: true
    },
    title: {
        type: String,
        required: true
    },
    year: {
        type: String,
        required: true
    },
    routeName: {
        type: String,
        required: true
    },
    onDrop: {
        type: Boolean,
        default: false,
    },
    toggleModel: {
        type: Function,
        required: true
    }   
});
const onPress = () => {
    // check auth
    if (props.isFree) {
        store.dispatch(actionTypes.setCurrentYear, props.year)
        store.dispatch(actionTypes.setCurrentTitle, props.title)
        router.push({ name: props.routeName });
        console.log("It's free");
    } else {
        if (store.getters.getHaveLicense) {
            store.dispatch(actionTypes.setCurrentYear, props.year)
            store.dispatch(actionTypes.setCurrentTitle, props.title)
            router.push({ name: props.routeName });
            console.log("Have License");
        } else {
            // go license page
            props.toggleModel();
            console.log("not have lincese");
        }
    }

};
const lock = () => {
    return !props.isFree && !store.getters.getHaveLicense;
};

</script>
<template>
    <button 
        @click="onPress()"
        class="w-3/4 md:w-2/3 my-4 py-2 bg-gradient-to-r to-cyan-500 from-blue-500 font-bold text-white text-xl text-center rounded-xl">
        {{title}} <Icon v-if="lock()" :path="mdiLock" size="20" class="text-yellow-400 text-xl ml-3"/>
    </button>
</template>